//переменные
$black: #000000;
$white: #FFF;
$body-color:#020510;
$section-color: #0A031C;

* {
    box-sizing: border-box;
    color:$white
  }

  body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: $body-color;
  }

.container{
    max-width: 1500px;
    margin: 0 auto;

}

img {
  max-width: 100%;
}

//movies

.section_title{
  color: $white;
}

.movies {
  position: relative;
    width: 1500px;
  margin: 0 auto;


  background-color: $section-color;

}
.movies__container{
  width: 1400px;
  margin: 0 auto;
  overflow-x: auto;
}
.movies__container::-webkit-scrollbar {
  display: none;
}
.movies-list {
  display: flex;
  flex-wrap: nowrap;
  width: 300%;
  list-style-type: none;
  margin: 0;
  padding: 0;


   li{
    align-items: flex-start;
    margin: 0 20px;
    width: 200px;
  }
}

.movies-list__link{
  display: inline-block;
}
.movies-list__img{
  border-radius: 12px;
  margin: auto 0;
}

.gallery-btn{
  position: absolute;
  top: 129px;
  width: 37px;
  height: 48px;
  border-radius: 5px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: rgba($color: #000000, $alpha: 0.5);
}

.gallery-btn--right {
  right: 20px;
}
.gallery-btn--left {
  left: 20px;
  transform: rotate(180deg);
}
.modal-window{
  width: 100%;
}
.modal-window__container{
  display: none;
}
.modal-window__container--active{
  display: flex;
  justify-content: space-between;
  max-width: 1163px;
  margin: 0 auto;
}
.modal-window__title{
  margin: 10px 0;
  font-size: 83px;
  font-weight: 900;
}
.modal-window__img {
  width: 395px;
  img{width: 100%;}
}
.modal-window__description{
  width: 700px;
}
.modal-window__text{
  font-size: 20px;
  font-weight: 500;
  line-height: 30px;
}